import axios from 'axios';
import { createStore } from 'vuex'

/*
버튼 누르면 state 데이터 바꿔보자
1. store.js state수정방법 정의
2. 수정하고 싶으면 store.js에 부탁
*/

const store = createStore({
  state(){
    return {
      name : 'kim',
      age : 20,
      likes : 30,
      좋아요눌렀니 : false ,
      more : {}
      
    }
  },
  mutations:{
    setMore(state,data){
      state.more = data
    },
    좋아요(state){
      if(state.좋아요눌렀니 == false){
        state.likes++;
        state.좋아요눌렀니 = true;
      }else{
        state.likes--;
        state.좋아요눌렀니 = false;
      }
      
    },
    // state 수정하는 곳
    이름변경(state){
      // 위에 들어가는 파라미터(state)는 바로위의 state를 가르킴
      state.name = 'park'
    },
    나이추가(state, data){
      // 위에 들어가는 파라미터(state)는 바로위의 state를 가르킴
      state.age += data
    }
  },
  actions : {
    // actions{}
    // ajax 하는 곳
    // 또는 오래걸리는 작업들

    getDate(context){
      // context = store
      // actions 에서 데이터를 변경하면 동일하게 mutations에서 변경되도록
      var url = 'https://codingapple1.github.io/vue/more0.json';
      
      axios.get(url).then((result)=>{
        
        console.log(result.data)
        context.commit('setMore',result.date)
      })
    },
  }
})

export default store